
import BScroll from "better-scroll"
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
name: "ScrollList",
components: {
Spinner
},
props: {
isRequest: {
type: Boolean,
default: false
},
pullDownRefresh: {
type: Boolean,
default: false
},
pullUpLoad: {
type: Boolean,
default: false
},
pageSize: {
type: Number,
default: 15
}
},
data() {
return {
isFail: false,
isShowData: this.isRequest,
autoTag: false,
upTag: 0,
loadTag: false,
downTag: -1 
};
},
methods: {
init(tag) {
if (typeof tag == 'boolean') {
this.isFail = true
} else {
this.isFail = false
this.isShowData = false
this.refresh()
}
},
refresh() {
this.$nextTick(() => {
this.scroll.refresh();
});
},
upShow(len) {
this.scroll.scrollTo(0, 0, 0)
if (typeof len === 'undefined') {
if (!this.isShowData) {
return
}
this.isFail = true
this.scroll.finishPullDown()
return
} else {
this.isFail = false
this.isShowData = false
}
if (len === 0) {

this.autoTag = true
this.downTag = -1
} else if (len == this.pageSize) {

this.downTag = 1
this.autoTag = false
} else {

this.downTag = 0
this.autoTag = false
}
},
upEnd() {
this.loadTag = false
this.upTag = 0
this.scroll.finishPullDown()
this.refresh()
},
downEnd() {
this.scroll.finishPullUp()
this.refresh()
}
},
mounted() {
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scrollH.style.minHeight = `${(this.$refs.wrapper.offsetHeight + 1)}px`
this.scroll.refresh()
}, 100)
if (!this.scroll) {
const pullDownRefresh = this.pullDownRefresh
? {
threshold: 60,
stop: 50
}
: false;
const pullUpLoad = this.pullUpLoad
? {
threshold: 0
}
: false;
/*
* 初始化
*/
this.scroll = new BScroll(this.$refs.wrapper, {
click: true,
pullDownRefresh,
pullUpLoad,
scrollbar: true
});
this.scroll.refresh()
/*
*监听手势y轴滑动距离
*/
this.scroll.on("scroll", pos => {
if (!this.pullDownRefresh) {
return
}
if (pos.y > 60) {
try {
this.$refs.refreshIcon.style.transform = 'rotate(0deg)'
this.$refs.refreshIcon.style.webkitTransform = 'rotate(0deg)'
} catch(e) {}
this.upTag = 1;
} else {
try {
this.$refs.refreshIcon.style.transform = 'rotate(180deg)'
this.$refs.refreshIcon.style.webkitTransform = 'rotate(180deg)'
} catch(e) {}
this.upTag = 0
}
});
/*
*下拉刷新监听
*/
this.scroll.on("pullingDown", () => {
this.loadTag = true;
let _self = this;
this.$emit("show-data", {
type: 0,
cb(len) {
_self.upShow(len)
_self.upEnd(len)
}
});
});
/*
*上拉刷新监听
*/
this.scroll.on("pullingUp", () => {
let _self = this;
if (this.downTag === 0 || this.downTag === -1) {
return;
}
this.$emit("show-data", {
type: 1,
cb(len) {
if (typeof len === 'undefined') {
return
}
_self.downEnd()
if (len < this.pageSize) {

_self.autoTag = false
_self.downTag = 0
} else {

_self.downTag = 1
_self.autoTag = false
}
}
});
});
}
});
}
};



