export default {
  name: 'HeaderCom',
  props: {
    title: {
      type: String,
      default: ''
    },
    isBack: {
      type: Boolean,
      default: false
    },
    isBacPortal: {
      type: Boolean,
      default: false
    },
    headerBg: {
      type: String,
      default: '#d81e06'
    }
  },
  data () {
    return {
    }
  },
  methods: {
    back () {
      if (this.isBacPortal) {
        if (window.corNative) {
          window.corNative.closeWeb()
        } else {
          window.history.go(-1)
        }
      } else {
        this.$emit('go-back')
        this.$router.goBack()
      }
    }
  }
}



